body {
    background: -webkit-gradient(
            linear,
            left top,
            left bottom,
            from(white),
            to(rgb(63, 62, 62))
        )
        fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.container {
    background-color: white;
    border-radius: 2.5vh;
    display: inline-block;
    padding: 3vh;
    width: 59vh;
    position: relative;
}

.qr-code {
    background-color: rgb(234, 97, 120);
    padding-top: 3vh;
    padding-bottom: 5vh;
    border-radius: 2.3vh;
    width: 59vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

    position: inherit;
    top: -7vh;
}

.input {
    display: flex;
    flex-direction: column;
}
.qr-code h3 {
    color: white;
}

.qr-code h1,
.qr-code h3 {
    margin: 0;
}

.input input {
    outline: none;
    font-size: 1.1rem;
    padding: 2vh;
    margin-bottom: 3vh;
    border: 2px solid lightgrey;
    border-radius: 1vh;
}

input:focus {
    border: 2px solid black;
}

.input button {
    padding: 1.5vh;
    background-color: black;
    outline: none;
    border: none;
    color: white;
    font-size: 1.2rem;
    border-radius: 1vh;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.error {
    border: 2px solid red;
    border-radius: 1vh;
    padding: 2vh;
    margin-top: 1vh;
    display: flex;
    align-items: center;
    padding-left: 3vh;
}

.error img {
    margin-right: 1vh;
}

.options {
    display: flex;
    justify-content: space-between;
}

.download {
    background-color: green !important;
    width: 35vh;
}

a {
    text-decoration: none;
    color: white;
}

img {
    filter: invert(100%);
    height: 3.7vh;
    margin-right: 1.5vh;
}

.w-full {
    display: flex;
}
.error {
    color: red;
    font-weight: 500;
}
.error img {
    height: 3vh;
    /* filter: none !important */
    filter: hue-rotate(0deg);
}

.qr-code img {
    filter: none !important;
    height: 50vh !important;
    width: 50vh !important;
}
